<template>
   <header>
      <div class="headernav-first">
         <router-link :to="{name:'home'}">
           <h1></h1>
         </router-link>
         <nav>
           <ul>
             <li>
               <router-link :to="{name:'movie'}" style="color: #2387e8">电影</router-link>
             </li>
             <li>
                <router-link :to="{name:'book'}" style="color: #9f7860">图书</router-link>
             </li>
             <li>
                <router-link :to="{name:'radio'}" style="color: #e4a813">广播</router-link>
             </li>
             <li>
                <router-link :to="{name:'group'}" style="color: #2ab8cc">小组</router-link>
             </li>
           </ul>
           <span @click="openTalion"></span>
         </nav>
      </div>
      <slot></slot>
   </header>
</template>

<script>
export default {
  name: 'HeaderNav',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '@/style/bass.scss';
  header{
    position: fixed;
    z-index: 999;
    top:0;
    background-color: #fff;
    width:100%;
  }
  .headernav-first{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #f3f3f3;
  padding: 0 18px;
  align-items: center;
  height: 3rem;
  box-sizing: border-box;
}
.headernav-first nav{
  display: inline-block;
}
.headernav-first ul{
  display: inline-block;
  margin: 0;
  padding: 0;
}
.headernav-first a{
  text-decoration: none;
}
.headernav-first nav>*{
  vertical-align: middle;
}
.headernav-first nav li{
  list-style: none;
  font-size: 15px;
  margin-right: 19px;
  display: inline-block;
}
.headernav-first h1{
  color: #00b600;
  background: url(data:image/png;base64,) no-repeat;
  background-size: cover;
  height: 22px;
  width: 46px;
  display: inline-block;
  margin: 0;
  padding: 0;
}
.headernav-first nav span{
  font-size: 0;
  background:url(data:image/png;base64,) no-repeat;
  background-size: cover;
  position: relative;
  margin-top:4px;
  height: 16px;
  width: 24px;
  display:inline-block;
}
</style>



